import React from "react"
import { Link } from "gatsby"
import {TextField} from "@material-ui/core"
import Button from "@material-ui/core/Button"

import Layout from "../components/layout"
import SEO from "../components/seo"

class ThirdPage extends React.Component {

  constructor(props) {
    super(props);
    this.state = {
      jsonInput: '',
      pdfFileName: '',
      hint: ''
    }
  }

  handleJsonInputChanged(e) {
    this.setState({jsonInput: e.target.value})
  }

  handleConvertButtonClicked(e) {
    const jsonInput = this.state.jsonInput
    const pdfFileName = this.state.pdfFileName
    const hint = 'Ready for download'
    this.setState({
      jsonInput: jsonInput,
      pdfFileName: pdfFileName,
      hint: hint
    })
  }

  render() {
    return (
      <Layout headerTitle="Swagger2 PDF ">
        <SEO title="Swagger2 Json  PDF"/>
        <p>Swagger2 json</p>
        <div>
          <TextField id="filled-basic" label="JSON" variant="filled"
                     onChange={(e) => this.handleJsonInputChanged(e)}>
          </TextField>
        </div>
        <div>
          <Button variant="contained" color="primary" onClick={(e) => this.handleConvertButtonClicked(e)}>
            convert
          </Button>
        </div>

        <div>
          <p>{this.state.hint}</p>
        </div>
        <Link to="/">Go back to the homepage</Link>
      </Layout>
    )
  }
}

export default ThirdPage
